export type Note = {
  note: string;
  octave: number;
};

export type NoteRange = {
  lower: Note;
  upper: Note;
};

const NOTES = ['C', 'D', 'E', 'F', 'G', 'A', 'B'];

export const DEFAULT_RANGES = {
  treble: {
    lower: { note: 'C', octave: 3 },
    upper: { note: 'B', octave: 5 }
  },
  bass: {
    lower: { note: 'C', octave: 1 },
    upper: { note: 'B', octave: 3 }
  }
};

export const generateRandomNote = (
  range: NoteRange,
  lastNote: Note | null,
  noteCounts: Map<string, number>,
  excludeNotes: Note[] = [],
  correctRates?: Map<string, { correct: number; total: number }>
): Note => {
  const allPossibleNotes: Note[] = [];
  for (let octave = range.lower.octave; octave <= range.upper.octave; octave++) {
    for (const note of NOTES) {
      const currentNote = { note, octave };
      if (isNoteInRange(currentNote, range)) {
        allPossibleNotes.push(currentNote);
      }
    }
  }

  // 前回と同じ音、表示中の音を除外
  const filteredNotes = allPossibleNotes.filter((n) => {
    if (lastNote && n.note === lastNote.note && n.octave === lastNote.octave) return false;
    if (excludeNotes.some(e => e.note === n.note && e.octave === n.octave)) return false;
    return true;
  });

  // 出現回数が少ない音を優先し、正解率が低い音はさらに重みを上げる
  const weightedNotes = filteredNotes.map((note) => {
    const noteKey = `${note.note}${note.octave}`;
    const count = noteCounts.get(noteKey) || 0;
    let weight = 1 / (count + 1);
    if (correctRates && correctRates.has(noteKey)) {
      const { correct, total } = correctRates.get(noteKey)!;
      const rate = total > 0 ? correct / total : 0.5;
      // 正解率が低いほど重みを上げる（例: 0.1~1.0の範囲で逆数的に）
      weight *= 1.5 - rate; // rate=0→1.5倍, rate=1→0.5倍
    }
    return { note, weight };
  });

  const totalWeight = weightedNotes.reduce((sum, { weight }) => sum + weight, 0);
  let random = Math.random() * totalWeight;

  for (const { note, weight } of weightedNotes) {
    random -= weight;
    if (random <= 0) {
      return note;
    }
  }

  return filteredNotes[0];
};

export const isNoteInRange = (note: Note, range: NoteRange): boolean => {
  const noteValue = NOTES.indexOf(note.note) + note.octave * 7;
  const lowerValue = NOTES.indexOf(range.lower.note) + range.lower.octave * 7;
  const upperValue = NOTES.indexOf(range.upper.note) + range.upper.octave * 7;
  return noteValue >= lowerValue && noteValue <= upperValue;
};

export const noteToString = (note: Note): string => {
  return `${note.note}${note.octave}`;
};

export const stringToNote = (str: string): Note | null => {
  if (!str || str.length < 2) return null;
  const note = str[0].toUpperCase();
  const octave = parseInt(str.slice(1));
  if (!['C', 'D', 'E', 'F', 'G', 'A', 'B'].includes(note) || isNaN(octave)) return null;
  return { note, octave };
}; 